import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {CityService} from '../../services/city/city.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { LoadingController,AlertController } from '@ionic/angular';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import {FileTransfer, FileUploadOptions, FileTransferObject} from '@ionic-native/file-transfer/ngx/';
import {environment} from '../../../environments/environment';

@Component({
    selector: 'app-signup',
    templateUrl: './signup.page.html',
    styleUrls: ['./signup.page.scss'],
})
export class SignupPage implements OnInit {
    public user:any;
    public step:number;
    constructor(public authService: AuthenticationService, private cityService:CityService, private iab: InAppBrowser, private transfer: FileTransfer, 
        private camera: Camera, public loadingController: LoadingController, public alertController: AlertController, private route:ActivatedRoute) {
        this.user = {permission:"user", email:"", password:"", error:"", location:"",loading:false, tos:true};

        this.step = 1;
        
        let profileType = this.route.snapshot.params['type'];
        
        this.user.type = profileType;
        
    }

    ngOnInit() {
    }
    
    
    public changeStep(step){
        this.step = step;
    }
    
    
    public signup(){
        this.user.loading = true;
        this.user.error = "";
        
        if (!this.user.email || !this.user.first_name || !this.user.last_name){
            this.user.loading = false;
            this.user.error = "Required fields not filled in. ";            
        }
        
        if (!this.user.tos){
            this.user.loading = false;
            this.user.error = "Please accept the Terms and Conditions";            
        }        
        
        //alert("Registering... Press ok to continue");
        this.authService.register(this.user).then(() => {
            //alert("Registration successful");
                this.user.loading = false;
                this.user = {permission:"babysitter", email:"", password:"", error:"", city:"",country:"New Zealand", latitude:-36.866667, longitude:174.766667, loading:false, tos:true};
                //this.authService.getUserData();
                this.authService.retreiveUserData().then(() => {
                    //alert("Retreived user data");
                }).catch(() => {
                    //alert("Failed to retreive user data");
                });
        }).catch((e) => {
        //alert("Registration failed. Press ok to see error. Screenshot this screen and send to me please.");
            //alert(JSON.stringify(e));
            this.user.loading = false;
            console.log(e);
            if (e && e.error){
                if (e.error.error){
                    for (var index in e.error.error.errors){
                        let error = e.error.error.errors[index];
                        this.user.error = this.user.error + error + " ";
                    }  
                }
                else{
                    this.user.error = e.error.message;
                }            
            }
            else{
                this.user.error = e.message;
            }

            
        })
  
    } 

      
    public openTos(ev){
        ev.preventDefault();
        ev.stopPropagation();
        this.iab.create("http://128.199.179.252/terms", '_system');
    }
    
    
    
    public changeImage(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {
                let loading;
                this.loadingController.create({
                  message: 'Your image is being uploaded. Please wait...',
                  duration: 20000
                }).then((el) => {
                    loading = el;
                    el.present();
                });           

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"media_file",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }
             
                
                fileTransfer.upload(image, encodeURI(environment.apiUrl + "/upload"), options).then((data) => {

                    if (data.response){
                        let response = JSON.parse(data.response);
                        this.user.avatar = response.file;  
                    }     
                    loading.dismiss();           
                }, (err) => {
                    this.alertController.create({
                        header: "Error",
                        subHeader: "There was a problem uploading your image",
                        message: JSON.stringify(err),
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    }).then((el) => {
                        el.present(); 
                    });
                    
                    loading.dismiss();
                                       
                })
      
                
            }, 
            err => {
                //console.log(err)
            });   
    }     
    
    

}
